<template>
  <b-container>
    <!-- <div class="d-flex justify-content-center">
      <bingo-table />
    </div> -->

    <f-checkbox-group v-model="selectElement" :options="elements">
      <template #label>屬性</template>
    </f-checkbox-group>

    <f-checkbox-group v-model="selectPattern" :options="patterns">
      <template #label>類型</template>
    </f-checkbox-group>

    <b-form-group>
      <template #label>搜尋</template>
      <b-form-input v-model="search" />
    </b-form-group>

    <gene-list @moving="test($event)" />
  </b-container>
</template>

<script>
import { mapState } from "vuex";

export default {
  data() {
    return {
      search: "",
    };
  },
  computed: {
    selectElement: {
      get() {
        return this.$store.state.selectElement;
      },
      set(value) {
        this.$store.commit("selectElement", value);
      },
    },
    selectPattern: {
      get() {
        return this.$store.state.selectPattern;
      },
      set(value) {
        this.$store.commit("selectPattern", value);
      },
    },
    ...mapState(["elements", "patterns"]),
  },
  components: {
    // "bingo-table": () => import("../components/bingo/bingo-table.vue"),
    "f-checkbox-group": () => import("../components/form/f-checkbox-group.vue"),
    "gene-list": () => import("../components/gene/gene-list.vue"),
  },
  methods: {},
};
</script>

